@import "common/mixins";
@import "common/variables";

.activity {
  margin-top: 40px;
}

.activity__container {

}
.activity__title {
  text-align: center;
}

.activity__body {
  display: flex;
  justify-content: space-evenly;
  align-items: start;
  flex-wrap: wrap;
  margin-top: 60px;

  @media screen and (max-width: $m) {
    margin-top: 0;
  }

  .activity__item {
    width: 300px;
    background-color: $bg-light;
    margin: 20px;
    padding: 20px 40px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 8px 25px 0 $shadow-light;
    box-sizing: border-box;

    &:nth-child(2n) {
      margin-top: 150px;
    }

    @media screen and (max-width: $m) {
      &:nth-child(n) {
        width: 100%;
        margin-top: 30px;
      }
    }

    @media screen and (max-width: $s) {
      &:nth-child(n) {
        padding: 20px 20px;
      }
    }

    .activity__item__img {
      height: 100px;
    }
    .activity__item__title {
      padding: 10px;
      font-size: 36px;
      font-weight: 600;
    }
    .activity__item__text {
      margin-top: 20px;
      line-height: 124.5%;

      @media screen and (max-width: $m) {
        font-size: 16px;
      }
    }
    .activity__item__button {
      @include primary-button();
      margin-top: 30px;
      margin-bottom: -36px;
    }
  }
}
