@import "common/mixins";
@import "common/variables";

.exercises {
  position: relative;

  .exercises__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 60px;

    @media screen and (max-width: $m) {
      flex-direction: column;
      margin-top: 50px;
    }

    @media screen and (max-width: $s) {
      padding: 0;
    }

    .exercises__body {
      width: 50%;
      padding: 0 100px;
      box-sizing: border-box;

      @media screen and (max-width: $l) {
        padding: 0 30px;
      }

      @media screen and (max-width: $m) {
        width: 100%;
      }

      @media screen and (max-width: $s) {
        padding: 0 20px;
      }


      .exercises__title {
        @media screen and (max-width: $m) {
          margin-top: 40px;
        }
      }

      .exercises__text {

      }

      .exercises__buttons-container {
        display: flex;
        align-items: center;

        .exercises__button {
          @include primary-button();
          margin: 20px;
        }
      }
    }
    .exercises__img {
      width: 50%;

      @media screen and (max-width: $m) {
        width: 70%;
      }

      img {
        max-width: 100%;
      }
    }
  }
}
