@import "common/mixins";
@import "common/variables";

.footer {
  width: 100%;
  padding: 30px 0;
  margin-top: 100px;
  background-color: $primary;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (max-width: $s) {
    margin-top: 40px;
  }

  div{
    padding: 10px;
  }

  .footer__links {
    display: flex;
    justify-content: center;

    a {
      display: block;
      padding: 4px 10px;
      border-right: 1px solid white;
      text-decoration: none;
      color: white;
      text-wrap: nowrap;

      &:last-child {
        border: none;
      }
    }
  }

  .footer_website {
    font-weight: 600;
    a {
      color: $text-accent;
    }
  }

  .footer__socials {
    display: flex;
    justify-content: center;

    .footer__socials__item {
      background-color: #ffffff;
      color: $primary;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 24px;
      width: 46px;
      height: 46px;
      margin: 0 10px;
      padding: 0;
      border-radius: 50%;
    }
  }
}
