@import "common/social_font";
@import "common/mixins";
@import "header";
@import "main";
@import "activity";
@import "workout";
@import "exercises";
@import "performance";
@import "footer";


body {
  margin: 0;
  padding: 0;
  color: $primary;
  box-sizing: border-box;
  font-family: 'Questrial', sans-serif;
  line-height: 22px;

  @media screen and (max-width: $m) {
    font-size: 18px;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h2 {
  font-size: 60px;
  font-weight: 600;
  line-height: 60px;
  margin: 0;
  margin-bottom: 30px;
  color: $primary;

  @media screen and (max-width: $m) {
    font-size: 38px;
    margin-bottom: 0;
    line-height: 40px;
  }
}

.secondary {
  color: $secondary;
}

.secondary-btn {
  @include primary-button();
  border: 2px solid $primary;
  background: none;
  color: $primary;

  &:hover {
    background: none;
  }
}



.background {
  background: linear-gradient(180deg, #396D89 -6.84%, rgba(241, 246, 250, 0.40) 36.82%,
              rgba(240, 246, 249, 0.33) 56.81%,
              rgba(240, 246, 249, 0.41) 88.01%,
              rgba(78, 125, 150, 0.70) 112.96%);
  position: relative;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: $primary;
}
