@import "common/mixins";
@import "common/variables";

.main {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;

  @media screen and (max-width: $l) and (max-aspect-ratio: 2 / 1) {
    flex-direction: column-reverse;
    justify-content: flex-end;
  }

  @media screen and (max-width: $xxs) {
    height: 100%;
  }

  .main__content {
    width: 60%;
    max-height: 100%;
    padding: 10px 10px 10px 100px;
    color: $text-accent;
    box-sizing: border-box;

    @media screen and (max-width: $l) {
      padding-left: 40px;
      width: 80%;
    }

    @media screen and (max-width: $m) {
      width: 100%;
      padding: 40px 80px;
    }

    @media screen and (max-width: $xs) {
      padding: 40px 30px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      box-sizing: border-box;
    }

    h1 {
      font-size: 64px;
      font-weight: 600;
      line-height: 60px;
      margin: 0;

      @media screen and (max-width: $m) {
        font-size: 48px;
      }
    }

    p {
      line-height: 24px;
    }

    .main__content__tips {
      display: flex;

      .main__content__tips__item-advantage, .main__content__tips__item-time {
        margin: 10px 40px;
        font-size: 30px;
        font-weight: 600;
        position: relative;
        display: flex;
        align-items: center;
        text-wrap: nowrap;

        @media screen and (max-width: $l) {
          font-size: 20px;
        }

        @media screen and (max-width: $m) {
          font-size: 20px;
        }

        &::before {
          content: '';
          position: absolute;
          left: -40px;
          width: 30px;
          height: 30px;
        }
      }

      .main__content__tips__item-advantage::before { background: url("/assets/img/main/ic_Advantage.png") bottom/cover no-repeat}
      .main__content__tips__item-time:before { background: url("/assets/img/main/ic_timer.png") bottom/cover no-repeat}
    }

    .main__content__start-button {
      @include primary-button();
      font-size: 22px;
      margin-top: 20px;
    }
  }

  .main__img {
    width: 1000px;
    height: 1000px;
    max-height: 100%;
    position: relative;

    @media screen and (max-width: $l) {
      width: 600px;
      height: 600px;
    }

    @media screen and (max-width: $m) {
      width: 600px;
      height: 600px;
    }

    @media screen and (max-width: $s) {
      width: 400px;
      height: 400px;
    }

    @media screen and (max-width: $xs) {
      display: none;
    }

    div[class^='main__img__'] {
      position: absolute;
    }

    .main__img__background {
      top: 3%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 100%;
      background: url("/assets/img/main/menBackgroundFull.png") center no-repeat;
      background-size: contain;

    }

    .main__img__men {
      top: 15%;
      left: 50%;
      transform: translateX(-50%);
      width: 70%;
      height: 70%;
      background: url("/assets/img/main/menFull.png") center no-repeat;
      background-size: contain;
    }
  }
}
