@import "common/mixins";
@import "common/variables";

.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;

  .top__logo {
    margin-left: 50px;
    padding: 10px 0;
    display: flex;
    align-items: center;

    @media screen and (max-width: $s) {
      margin-left: 10px;
    }

    .top__logo__img {
      img {
        width: 100px;
      }
    }

    .top__logo__text {
      padding-left: 10px;
      font-size: 28px;
      line-height: 28px;
      font-weight: 600;
    }
  }

  .nav {

    ul {
      display: flex;
      justify-content: space-between;

      @media screen and (max-width: $m) {
        //display: none;
        position: absolute;
        height: 100%;
        width: 40%;
        top: 102px;
        right: -40%;
        flex-direction: column;
        justify-content: flex-start;
        background: white;
        z-index: 1;
        transition-duration: 1s;



        @media screen and (max-width: $xs) {
          width: 90%;
          right: -90%;
        }
      }

      li {
        padding: 10px 30px;
        margin: 10px 20px;
        border-radius: 20px;
        font-weight: 600;
        transition: background-color 0.3s ease-in;

        a {
          color: $primary;
          transition: color 0.3s ease-in;
        }

        &:hover {
          background-color: $primary;
          transition: background-color 0.3s ease-in;

          a {
            color: $text-accent;
            transition: color 0.3s ease-in;
          }
        }

        @media screen and (max-width: $m) {
          padding: 20px 30px;
        }
      }
    }

    .open-menu__btn {
      display: none;
      width: 40px;
      height: 40px;
      margin-right: 20px;
      border: none;
      background: none;
      padding: 0;
      cursor: pointer;

      @media screen and (max-width: $m) {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
      }

      .top, .middle, .bottom {
        height: 3px;
        width: 100%;
        background-color: #396d89;
      }

      .top, .bottom {
        transition: .5s linear;
      }

    }

    &.open {
      ul {
        right: 0;
        transition-duration: 1s;
      }

      .open-menu__btn {
        justify-content: center;
        align-items: center;

        .middle {
          visibility: hidden;
        }

        .top {
          rotate: 45deg;
          transition: .5s linear;
        }

        .bottom {
          rotate: -45deg;
          transition: .5s linear;
          margin-top: -5px;
        }
      }
    }
  }
}
