@import "common/mixins";
@import "common/variables";

.workout {
  position: relative;

  .workout__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 60px;

    @media screen and (max-width: $m) {
      flex-direction: column-reverse;
      margin-top: 50px;
    }

    @media screen and (max-width: $s) {
      padding: 0;
    }

    .workout__body {
      width: 50%;
      padding: 0 100px;
      box-sizing: border-box;

      @media screen and (max-width: $l) {
        padding: 0 30px;
      }

      @media screen and (max-width: $m) {
        width: 100%;
      }

      @media screen and (max-width: $s) {
        padding: 0 20px;
      }

      .workout__title {
        @media screen and (max-width: $m) {
          margin-top: 40px;
        }
      }

      .workout__text {

      }

      .workout__buttons-container {
        display: flex;
        justify-content: space-around;
        align-items: center;

        .workout__btn {
          @include primary-button();
          margin: 20px;
        }

        .workout__secondary-btn {
          @include secondary-button;
          margin: 20px;
        }
      }
    }
    .workout__img {
      width: 50%;

      @media screen and (max-width: $m) {
        width: 70%;
      }

      img {
        max-width: 100%;
      }
    }
  }
}
