@import "common/mixins";
@import "common/variables";

.performance {
  margin-top: 150px;

  @media screen and (max-width: $m) {
    margin-top: 50px;
  }

  .perf__main-title {
    text-align: center;
  }

  .perf_row {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 10px;
    padding-top: 100px;


    @media screen and (max-width: $m) {
      flex-direction: column;
      padding: 50px 20px;

      &:nth-child(3) {
        flex-direction: column-reverse;
      }

      &:nth-child(4) {
        padding: 0;
      }
    }



    .perf__content {
      max-width: 600px;

      @media screen and (max-width: $l) {
        padding: 0 20px;
      }

      .perf__title {
        @media screen and (max-width: $s) {
          font-size: 28px;
          line-height: 28px;
          text-align: center;
        }
      }

      .perf__text {

      }

      .perf__buttons-container {
        display: flex;
        align-items: center;

        .perf__button {
          @include primary-button();
        }
      }
    }

    .perf__activity-diagram {

      .perf__activity-diagram__wrapper {
        width: 708px;
        height: 440px;
        border: 3px solid $primary;
        border-radius: 6px;
        padding: 20px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @media screen and (max-width: $m) {
          margin-top: 50px;
        }

        @media screen and (max-width: $s) {
          width: 340px;
          height: 300px;
        }

        .perf__activity-diagram__top {
          display: flex;
          justify-content: space-between;

          .perf__activity-diagram__title {
            font-size: 22px;
            font-weight: 600;
          }

          .perf__activity-diagram__label {
            opacity: 0.5;
            font-weight: 600;
          }
        }

        .perf__activity-diagram__indicators {
          display: flex;
          justify-content: space-evenly;
          height: 75%;

          .perf__activity-diagram__indicators__item {
            display: flex;
            flex-direction: column;
            align-items: center;

            .label {
              font-weight: 600;
              opacity: 0.5;
              text-transform: uppercase;
              margin-bottom: 5px;
            }

            .indicator-wrapper {
              display: flex;
              justify-content: end;
              flex-direction: column;
              height: 100%;
              width: 8px;
              border-radius: 2px;
              background-color: rgba(#25B6ED, 0.2);

              .rest {
                border-radius: 4px;
                background-color: rgba($bg-primary, 0.5);
                width: 100%;
              }
              .yoga {
                border-radius: 4px;
                background-color: #396D89;
              }
            }
          }
        }

        .perf__activity-diagram__legend {
          display: flex;
          justify-content: space-between;

          .perf__activity-diagram__legend__item {
            font-weight: 600;
            display: flex;
            align-items: center;

            &::before {
              content: '';
              margin-right: 10px;
              display: inline-block;
              width: 17px;
              height: 17px;
              border-radius: 50%;
              background-color: #396D89;
            }

            &:nth-child(1)::before {
              background-color: #396D89;
            }

            &:nth-child(2)::before {
              background-color: rgba($bg-primary, 0.5);
            }

            &:nth-child(3)::before {
              background-color: rgba(#25B6ED, 0.2);;
            }
          }
        }
      }
    }

    .perf__meals-diagram {

      .perf__meals-diagram__wrapper {
        width: 538px;
        height: 560px;
        background-color: $bg-primary;
        border-radius: 6px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color: white;

        @media screen and (max-width: $m) {
          margin-top: 50px;
        }

        @media screen and (max-width: $s) {
          width: 340px;
          height: 360px;
        }

        .perf__meals-diagram__top {
          padding: 20px;
          display: flex;
          justify-content: space-between;

          .perf__meals-diagram__title {
            font-size: 22px;
            font-weight: 600;
          }
          .perf__meals-diagram__label {
            opacity: 0.5;
            font-weight: 600;
          }
        }

        .perf__meals-diagram__kcal {
          font-size: 22px;
          font-weight: 600;
          padding: 20px;

          .number {
            font-size: 40px;
          }
        }

        .perf__meals-diagram_chart {
          img {
            width: 100%;
          }
        }

        .perf__meals-diagram__bottom {
          display: flex;
          justify-content: space-between;
          padding: 30px;
          text-transform: uppercase;
          font-weight: 600;
        }
      }
    }

    .perf__indexes {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      color: white;
      width: 80%;

      .perf__indexes__item {
        margin: 20px;
        width: calc((100% - 120px)/3);
        min-width: 300px;
        min-height: 100px;
        padding: 15px 20px;
        background-color: $bg-primary;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-sizing: border-box;

        .perf__indexes__item__row {
          display: flex;
          justify-content: space-between;
          align-items: end;

          &:last-child {
            margin-top: 20px;
          }

          .perf__indexes__item__row__title {
            font-size: 22px;
            font-weight: 600;
          }
          .perf__indexes__item__row__percentages {
            opacity: 0.5;
            font-weight: 600;
          }
          .perf__indexes__item__row__variable {
            .number {
              font-size: 32px;
            }
          }
          .perf__indexes__item__row__chart {
            img {
              height: 50px;
            }
          }
        }
      }
    }
  }
}
