@import "variables";

@mixin button {
  cursor: pointer;
  padding: 15px 30px;
  border-radius: 35px;
  font-size: 18px;
}

@mixin primary-button() {
  @include button;
  border: none;
  background: linear-gradient(180deg, #4E7D96 0%, #396D89 100%);
  color: $text-accent;

  &:hover {
    background: $primary;
  }

  &:active {
    background: linear-gradient(0deg, #4E7D96 0%, #396D89 100%);
  }
}

@mixin secondary-button() {
  @include button;
  border: 2px solid $primary;
  background: none;
  color: $text-primary;

  &:active {
    background-color: rgba(78,125,150,.05);
  }
}
